import React from 'react';
import SearchBar from '../search-bar/SearchBar';
import classes from './main-screen.module.css';

export default function MainScreen() {
  return (
    <div className={classes.main_screen_container}>
      <div className={classes.main_screen_content}>
        <div className={classes.main_info}>
          <h1 className={classes.main_header}>  </h1>
          <p className={classes.main_text}>
                     
          </p>
          <p className={classes.main_text}>
               :<span className={classes.main_count}> 13 438</span>
            <br />
            :<span className={classes.main_count}> 4 272</span>
          </p>
          <div className={classes.search_panel}>
            <SearchBar placeholder="  "></SearchBar>
            <button className={classes.search_btn}></button>
          </div>
        </div>
        <div className={classes.main_image}>
          <img src="/main-image.png" alt="main img"></img>
        </div>
      </div>
    </div>
  );
}
